@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Noto+Sans&family=Poppins:wght@300;400;500;700&family=Roboto&display=swap');

*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}

// Mixins
@mixin heading-style {
    font-size: 200%;
    font-weight: 400;
    width: 200px;
    margin: 0 auto 10px auto;
    color: #fff;
    background-color: deeppink;
    position: relative;
    z-index: 10;
    box-shadow: -5px 5px 1px rgba(255, 20, 145, 0.622);
    border-radius: 5px;
}
@mixin paragraph-style {
    padding: 15px 0;
    text-align: justify;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    line-height: 1.6;
}

body{
    font-family: 'Poppins', sans-serif;
}
// App
div.App{
    transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

// Home
div.home{
    padding: 0;
    margin: 0;
    position: relative;
    div.overlay-wrapper{
        position: fixed;
        width: 100%;
        height: 100%;
        top: -100%;
        left: 0;
        background-color: rgba(0, 0, 0, 0.622);
        z-index: 10;
        transition: all .3s ease-in-out;
        div.overlay{
            width: 100%;
            background-color: #fff;
            height: 100%;
            // box-shadow: -2px 5px 10px rgba(0,0,0,.8);
            div.contents{
                header{
                    padding: 20px 30px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    img{
                        height: 25px;
                    }
                    span{
                        display: flex;
                        width: 30px;
                        height: 30px;
                        border-radius: 50%;
                        align-items: center;
                        cursor: pointer;
                        justify-content: center;
                        background-color: rgba(0,0,0,.1);
                        color: rgba(255, 255, 255, 0.622);
                    }
                }
                button{
                    margin: 20px 0 20px 20px
                }
                ul{
                    list-style: none;
                    li{
                        margin: 5px 0;
                        a{
                            padding: 10px 15px;
                            display:block;
                            text-decoration: none;
                            color: rgba(0, 0, 0, 0.622);
                            &:hover{
                                background-color: rgba(0, 0, 0, 0.1);
                            }
                        }
                    }
                }
                div.content{
                    position: absolute;
                    bottom: 0;
                    left:0;
                    width: 100%;
                    display: flex;
                    padding: 10% 5%;
                    justify-content: center;
                    gap: 30px;
                    .email, .phone{
                        h4{
                            color:rgba(0, 0, 0, 0.722);
                            font-family: "Montserrat", sans-serif;
                            font-weight: 400;
                            font-size: 16px;
                        }
                        h2{
                            font-family: "Montserrat", sans-serif;
                            font-weight: bold;
                            font-size: 150%;
                            color: rgba(0, 0, 0, 0.5);
                        }
                    }
                }
            }
        }
    }
    div.overlay-wrapper.toggle{
        top: 0;
    }
    // OVerlay
    div.overlay-wrapper{
        display: none;
    }
    @media (max-width:776px){
        div.overlay-wrapper{
            display: block;
            div.overlay{

                div.contents{
                    div.content{
                        .email, .phone{
                            h4{
                                font-family: "Montserrat", sans-serif;
                                font-weight: 400;
                                font-size: 12px;
                            }
                            h2{
                                font-family: "Montserrat", sans-serif;
                                font-weight: bold;
                                font-size: 100%;
                            }
                        }
                    }
                }
            }
        }
    }
}

// Swiper
.mySwiper{
        position: relative;
        width: 100%;
        min-height: 100vh;
        .swiper-wrapper{

        }
    .slide{
        position: relative;
        width: 100%;
        min-height: 100vh;
        div.contents{
            display: flex;
            background-color: rgba(0, 0, 0, 0.622);
            min-height: 100vh;
           div.text{
            padding: 10% 5%;
            h1{
                font-size: 300%;
                font-weight: bold;
                font-family: "Montserrat", sans-serif;
                color: #ddd;
                margin-bottom: 20px;
                text-transform: uppercase;
            }
            h3{
                font-size: 100%;
                font-weight: 600;
                font-family: "Montserrat", sans-serif;
                color: rgba(255, 255, 255, 0.622);
                margin-bottom: 20px;
                // text-transform: uppercase;
            }
            p{
                font-size: 18px;
                font-weight: 400;
                font-family: "Montserrat", sans-serif;
                color: rgba(255, 255, 255, 0.622);
                margin-bottom: 20px;
            }
           }
        }
    }
    .slide-one{
        background-image: url("../images/Quran/quran3.jpg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    .slide-two{
        background-image: url("../images/Quran/quran4.jpg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    .slide-three{
        background-image: url("../images/Quran/quran2.jpg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    .swiper-pagination-bullet{
        width: 40px;
        height: 5px;
        border-radius: 1px;
        background-color: #fff;
    } 
    .swiper-pagination-bullet-active{
        background-color: deeppink;

    }
    
    @media (max-width:776px){
        .slide-one, .slide-two, .slide-three{
            min-height: 100vh;
            div.contents{
                display: flex;
                min-height: 100vh;
                text-align: center;
                align-items: center;
            }
        }
        div.slide{
            div.contents{
                div.text{
                    h1{
                        font-size: 150%;
                    }
                }
            }
        }
    }
}
@media (max-width:776px){
    .mySwiper{
        min-height: 100vh;

        .swiper-wrapper{
            min-height: 100vh;
        }
    }
}
// Navabr
div.navbar{
    display: flex;
    flex-direction: column;
    header{
        div.contact-bar{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 1% 5%;
            background-color: #eee;
            a.logo{
                width: 300px;
                height: 30px;
                transition: all .3s cubic-bezier(0.215, 0.610, 0.355, 1);
                &:hover{
                    opacity: .8;
                }
            }
            div.content{
                display: flex;
                justify-content: center;
                gap: 40px;
                .email, .phone{
                    padding: 10px 0;
                    flex: 1;
                    h2{
                        font-size: 22px;
                        font-weight: bold;
                        font-family: 'Montserrat', sans-serif;
                    }
                    h4{
                        font-size: 16px;
                        font-weight: 400;
                        font-family: 'Montserrat', sans-serif;
                    }
                }
                .email{
                    border-right: 1px solid rgba(0,0,0,.1);
                    padding-right: 30px;
                }
            }
        }
        nav{
            display: flex;
            background-color: #fff;
            justify-content: space-between;
            padding: .5% 5%;
            span{
                display: none;
            }
            ul{
                display: flex;
                list-style: none;
                align-items: center;
                justify-content: center;
                li{
                    a{
                        color: #333;
                        font-size: 16px;
                        font-weight: 500;
                        text-decoration: none;
                        padding: 10px;
                        transition: all .3s cubic-bezier(0.215, 0.610, 0.355, 1);
                        position: relative;
                        text-align: center;
                        border-right: 1px solid rgba(0,0,0,.2);
                       &:hover{
                            &::after{
                                content: "";  
                                position: absolute; 
                                display: block;
                                width:100%;
                                height: 4px;
                                border-radius: 1px;
                                bottom: 0;
                                transition: all .3s cubic-bezier(0.215, 0.610, 0.355, 1);
                                left: 0;
                                background-color: #333;
                            }
                       }
                    }
                    a:last-child{
                        border-right: none;
                    }
                }
            }
        }
        @media (max-width:776px){
            div.contact-bar{
                display: none;
            }
            nav{
                display: flex;
                align-items: center;
                span{
                    display: block;
                    color: #333;
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    line-height: 1.5;
                    img{
                        height: 25px;
                        margin-left: 20px;
                    }
                }
                ul, button{
                    display: none;
                }
            }
        }
    }
   
}

div.navbar.hide-navbar{
    background-color: #231903;
    top: -100px;
    transition: all .3s cubic-bezier(0.39, 0.575, 0.565, 1);
}

// Mission

div.mission{
    padding: 2% 10%;
    text-align: center;
    background-color: #eee;
    display: flex;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    justify-content: center;
    color: #333;
    div.mission-contents{
        // background-color: rgba(0,0,0,.5);
        h1{
            @include heading-style
        }
        p{
            @include paragraph-style
        }
        div.content{
            display: flex;
            align-items: center;
            img{
                width: 200px;
                height: 200px;
                object-fit: cover;
                border-radius: 50px;
                margin-right: 50px;
            }
        }
        @media (max-width:776px){
            div.content{
                flex-direction: column;
                padding: 15px 0;
                img{
                    width: 100%;
                    height: 200px;
                }
            }
        }
    }
    @media (max-width:776px){
        div.mission-contents{
            padding-top: 20px;
            h1{
                font-size: 150%;
            }
            div.content{
                flex-direction: column;
                align-items: center;
                padding: 15px 0;
                img{
                    margin-right: 0;
                    margin-top: 10px;
                    margin-bottom: 10px;
                    width: 100%;
                    height: 200px;
                }
            }
        }
    }
}

// Courses
div.courses{
    padding: 5% 10%;
    width: 100%;
    div.course-contents{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(4, 1fr);
        justify-content: center;
        gap: 30px;
        div.course{
            div.card{
                padding: 15px;
                background-color: #eee;
                border-radius: 5px;
                div.title{
                    width:100%;
                    height: 100%;
                    img{
                        width:100%;
                        height: 200px;
                        object-fit: cover;
                    }
                    h2{
                        font-size: 100%;
                        font-family: "Montserrat", sans-serif;
                        font-weight: bold;
                        margin: 15px 0;
                    }
                }
                div.body{
                    div.what{
                        span.what-text{
                            display: flex;
                            align-items: center;
                            margin: 10px 0;
                            span:nth-child(1){
                                padding: 5px;
                                border-radius: 50%;
                                background-color: #17a43b;
                                margin-right: 10px;
                                line-height: 1.5;
                            }
                            span:nth-child(2){
                                line-height: 1.5;
                                font-size: 12px;
                                color: rgba(0,0,0,.7);

                            }
                        }
                    }
                    a.enroll{
                        text-decoration: none;
                        display: inline-block;
                        color: #fff;
                        opacity: .8;
                        font-size: 12px;
                        font-weight: 400;
                        font-family: 'Montserrat', sans-serif;
                        background-color: #17a43b;
                        padding: .4em 1em;
                    }
                }
            }
        }
        div.course-one{

        }
        div.course-two{

        }
        div.course-three{

        }
        div.course-four{

        }
        div.course-five{

        }
        div.course-six{

        }
        div.course-seven{

        }
        div.course-eight{

        }
        div.course-nine{

        }
    }

    @media (max-width:1076px) {
        div.course-contents{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
        }
    }
    @media (max-width:950px) {
        div.course-contents{
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
    }
    @media (max-width:660px) {
        div.course-contents{
            display: grid;
            grid-template-columns: repeat(1, 1fr);
        }
    }
}

div.feedbacks{
    padding: 2% 10%;
    background-color: #eee;
    h1{
        margin-left: 25px;
        color: rgba(0, 0, 0, 0.622);
        font-weight: 500;
        display: inline-block;
        text-align: center;
    }
    .mySwiper-navigation{
        .swiper-wrapper{
            
        }
    }

    div.feedback{
        min-width: 400px;
        margin: 25px 50px;
        color: #333;
        display: flex;
        justify-content: center;
        div.card{
            background-color: #fff;
            padding: 1em 3em;
            border-radius: 10px;
            box-shadow: 0 2px 5px rgba(0,0,0,.8);
            div.header{
                display: flex;
                align-items: center;
                img{
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                }
                div.name-address{
                    margin-left: 15px;
                    h4{
                        font-size: 100%;
                        font-weight: 500;
                    }
                    h6{
                        font-size: 10px;
                        font-weight: 400;
                        color: rgba(0, 0, 0, 0.622);
                        display: flex;
                        align-items: center;
                        img{
                            width: 10px;
                            height: 10px;
                            margin-right: 5px;
                        }
                    }
                }
            }
            div.body{
                p{
                    font-size: 14px;
                    font-weight: 400;
                    color: #333;
                    opacity: .5;
                    padding: 15px 0;
                }
            }
        }
    }

    div.swiper-pagination{
        bottom: 0px;
        .swiper-pagination-bullet{
            width: 40px;
            height: 5px;
            border-radius: 1px;
            background-color: #fff;
        } 
        .swiper-pagination-bullet-active{
            background-color: #fff;
    
        }
    }
}

@media (max-width:776px){
    div.feedbacks{
        padding: 5% 2%;
    } 
}
// Pricing
div.facilites{
    padding: 5% 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    h1{
        font-size: 200%;
        font-family: "Montserrat", sans-serif;
        font-weight: bolder;
        text-align: center;
        margin-bottom: 20px;
        color: rgba(0,0,0,.8);
    }

    div.contents{
       display: grid;
       grid-template-columns: repeat(2, 1fr);
       justify-content: center;
       gap: 10px;
       margin: 20px 0;
        div.facility{
            svg{
                margin-right: 10px;
            }
            display: flex;
            align-items: flex-start;
            color: rgba(0, 0, 0, 0.622);
        }
    }
    // Media
    @media (max-width:776px){
        div.contents{
            grid-template-columns: repeat(1, 1fr);
            gap:20px;
        }
    }
}
@media (max-width:776px){
    div.facilites{
        padding: 5% 5%;
    }
}
// Footer
div.footer{
    padding: 5% 5% 1% 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #305973;
    div.footer-contents{
        display: flex;
        gap: 30px;
        justify-content: center;
    }
    div.quick-links{
        display: flex;
        flex-direction: column;
        ul{
            list-style: none;
            li{
                a{
                    text-decoration: none;
                    color: #fff;
                    display: block;
                    margin: 10px 0;
                    opacity: .8;
                    transition: all .3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
                    &:hover{
                        text-decoration: underline;
                        opacity: .8;
                    }
                }
            }
        }
    }
    div.contact{
        display: flex;
        flex-direction: column;
        div.address, div.email, div.phone{
            margin: 10px 0;
            display: flex;
            align-items: flex-start;
            color: #fff;
            opacity: .8;
            svg{
                margin-right: 15px;
                width: 10%;
            }
            span{
                width: 90%;
            }
        }
    }
    div.social-medias{
        display: flex;
        flex-direction: column;
       div.icons{
        margin-top: 10px;
        a{
            text-decoration: none;
            color: #fff;
            opacity: .8;
            width: 30px;
            height: 30px;
            margin-right: 10px;
            transition: all .3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
            &:hover{
                opacity: .8;
            }
        }
       }
    }
    h3{
        font-size: 100%;
        color: #fff;
        font-family: "Montserrat", sans-serif;
        font-weight: bold;
        position: relative;
        margin-bottom: 10px;
        &::after{
            position: absolute;
            content: "";
            display: block;
            left: 0;
            bottom: -5px;
            width: 40px;
            height: 2px;
            z-index: 15;
            background-color: #fff;
        }
    }
    div.copyright{
        color: #fff;
        opacity: .5;
        a{
            color: #fff;
            opacity: .5;
            text-decoration: none;
            transition: all .3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
            &:hover{
                opacity: .7;
                text-decoration: underline;
            }
        }
    }
    @media (max-width:776px){
        div.footer-contents{
            display: block;
        }
        div.quick-links, div.contact, div.social-medias{
            margin: 20px 0;
        }
    }
}
@media (max-width:776px){
    div.footer{
        padding: 5% 5% 5% 5%;
        div.footer-contents{
            div.map{
                display: flex;
                justify-content: center;
                width: 80%;
                margin: auto;
            }
        }
    }
}